/**
 * formstate
 * state
 * - field
 * - id id
 * - name   company.name  company["name"]  name
 * - groupName   company.name  company["name"]  name
 * - groupIndex Index
 * - label 
 * - rule 
 * - interceptor 
 * - noTrim 
 * - fieldRef 
 * */
import {clone} from 'lodash';
import ruleValidate from './ruleValidate';

class Field {
  constructor({ id, name }) {
    this.id = id;
    this.name = name;
    this.isPass = false;
    this.validate = {};
  }

  setInfo({ groupName, groupIndex, label, rule, interceptor, noTrim, fieldRef, errMsg }) {
    this.groupName = groupName;
    this.groupIndex = groupIndex;
    this.label = label;
    this.rule = rule;
    this.interceptor = interceptor;
    this.noTrim = noTrim;
    this.fieldRef = fieldRef;
    this.errMsg = errMsg;
    return this;
  }

  deleteValue() {
    delete this.value;
    return this;
  }

  setValue(value) {
    this.value = value;
    return this;
  }

  clone() {
    return clone(this);
  }

  setValidateStatus({ status, msg = '', validateData }) {
    this.validate = {
      status, msg, validateData: Object.assign({}, this.validate.validateData, validateData)
    };
    return this;
  }

  async runValidate(rules, getFormData) {
    const validate = await ruleValidate({
      field: this.clone(), value: this.value, formRules: rules, getFormData
    });
    this.isPass = validate.result;
    this.validate = {
      status: validate.result === true ? 1 : 2,
      msg: validate.errMsg,
      validateData: Object.assign({}, validate.data)
    };
    return this;
  }
}

export default Field;
